/**
* @description This component shows a proper state icon for invalid routes
*
* @method render
*
* @author Armin Eslami
* @since File available since Release 1.0.0
* @version 1.0.0
*
*/

import React, { Component } from 'react';

import { COMPASS } from 'components/StatesIcons';
import { Global as GlobalStrings } from 'i18n/Persian';

export class InvalidRoute extends Component {
  render() {
    return (
      <div
        style={{
          position: 'absolute',
          margin: 'auto',
          left: 0,
          right: 0,
          top: '15%',
          width: 300,
          textAlign: 'center',
          color: '#999999'
        }}
      >
        {COMPASS}
        <p style={{ fontSize: '5rem', margin: 0 }}>۴۰۴</p>
        <p style={{ margin: 0, direction: 'rtl' }}>
          {GlobalStrings.pageNotFound}
        </p>
      </div>
    );
  }
}

export default InvalidRoute;



